<template>
  <div class="myAddress mescroll">
    <div class="list" id="list">
      <div class="list-data" id="listData">
        <div class="item" v-for="(item,index) in list" :key="index">
          <van-swipe-cell :right-width="65" async-close :disabled="$route.query.type =='1'?true:false">
            <div class="flex">
              <div class="flex-2" @click="choose(item)">
                <div class="info">
                  <div class="name">{{item.userBcUsername}}</div>
                  <div class="telno">{{item.userBcNumber}}</div>
                  <div class="default" v-if="item.isDefault == 1">默认</div>
                </div>
                <div class="addr">{{item.bcName}}</div>
                <div class="addr">{{item.bcTypeName}}</div>
              </div>
              <!-- <img src="../../assets/img/icon/bianji.png" @click="edit(item.id)" /> -->
            </div>
            <div slot="right" class="del" @click="del(item.bcCode)">移除</div>
          </van-swipe-cell>
        </div>
      </div>
    </div>
    <div class="btnWrap">
      <div class="btn" bindtap="add" @click="add">新增收款账号</div>
    </div>
  </div>
</template>
<script>
import { SwipeCell, Dialog } from "vant";
import { getBankcardList, delBankcard } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [SwipeCell.name]: SwipeCell
  },
  data() {
    return {
      mescroll: null,
      list: []
    };
  },
  methods: {
    choose(item) {
      if (this.$route.query.type == 1) {
        sessionStorage.setItem("bankcard", JSON.stringify(item));
        this.$router.back();
      } else {
        this.$router.push({ name: "addBankcard", query: { id: item.bcCode } });
      }
    },
    add() {
      this.$router.push({ name: "addBankcard" });
    },
    del(id) {
      let that = this;
      Dialog.confirm({
        title: "提示",
        message: "确认删除此账号？"
      })
        .then(() => {
          // on confirm
          const loading = this.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true // 禁用背景点击
          });
          delBankcard({
            id: id
          }).then(res => {
            loading.clear();
            if (res.code == 0) {
              that.$toast("删除成功");
              that.mescroll.resetUpScroll();
            } else {
              that.$toast(res.msg);
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize
      };
      getBankcardList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.msg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.total
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "收款账号";
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.myAddress {
  position: relative;
  height: 100%;
  .list {
    .item {
      padding: 0 10px;
      border-bottom: 1pt solid #efefef;
      height: 130px;
      .flex {
        height: 130px;
        display: flex;
        align-items: center;
        .flex-2 {
          flex: 2;
          margin-right: 20px;
          line-height: 30px;
        }
      }
      .info {
        display: flex;
        font-size: 18px;
        align-items: center;
        .name {
          font-weight: bold;
          margin-right: 15px;
        }
        .telno {
          font-weight: bold;
          margin-right: 15px;
        }
        .default {
          background: $base-color;
          color: #fff;
          font-size: 12px;
          width: 50px;
          height: 20px;
          line-height: 20px;
          border-radius: 20px;
          text-align: center;
          display: inline-block;
        }
      }
      .addr {
        font-size: 15px;
        color: #999;
        line-height: 25px;
        margin-top: 5px;
      }
      img {
        width: 18px;
        margin-right: 10px;
      }
    }
  }
  .btnWrap {
    background: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    .btn {
      background: $base-color;
      color: #fff;
      font-size: 15px;
      text-align: center;
      height: 50px;
      line-height: 50px;
      letter-spacing: 1px;
    }
  }
}
</style>
<style lang="scss">
.myAddress {
  .van-swipe-cell__right {
    background: $base-color;
    color: #fff;
    height: 100px;
    text-align: center;
    width: 65px;
    font-size: 15px;
  }

  .del {
    width: 10px;
    margin: 28px auto 0;
  }
}
</style>
